input[type=range] {
    -webkit-appearance: none;
    margin: 18px 0;
    // width: 100%;

    &:focus {
      outline: none;

      &::-webkit-slider-runnable-track {
        background: #367ebd;
      }
    }

      &::-webkit-slider-runnable-track {
        width: 100%;
        height: 8.4px;
        box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
        background: #3071a9;
        border-radius: 1.3px;
        border: 0.2px solid #010101;
        cursor: pointer;
        animate: 0.2s;
      }

      &::-webkit-slider-thumb {
        width: 20px;
        height: 36px;
        margin-top: -14px;
        box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
        border: 1px solid #888;
        border-radius: 10px;
        background: #ffffff;
        -webkit-appearance: none;
        cursor: pointer;
      }

      &::-moz-range-thumb {
        box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
        border: 1px solid #777;
        height: 36px;
        width: 206px;
        border-radius: 10px;
        background: #ffffff;
        cursor: pointer;
      }

      &::-moz-range-track {
        width: 100%;
        height: 8.4px;
        cursor: pointer;
        animate: 0.2s;
        box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
        background: #3071a9;
        border-radius: 1.3px;
        border: 0.2px solid #010101;
      }

      &::-ms-track {
        width: 100%;
        height: 8.4px;
        cursor: pointer;
        animate: 0.2s;
        background: transparent;
        border-color: transparent;
        border-width: 16px 0;
        color: transparent;
      }
}


input[type=range]
input[type=range]::-ms-fill-lower {
  background: #2a6495;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
input[type=range]::-ms-fill-upper {
  background: #3071a9;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
input[type=range]::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #ffffff;
  cursor: pointer;
}
input[type=range]:focus::-ms-fill-lower {
  background: #3071a9;
}
input[type=range]:focus::-ms-fill-upper {
  background: #367ebd;
}

button {
  margin: 2vmin 0 0 0;
  padding: 1vmin 3vmin;

  font-family: 'Noto Sans', sans-serif;
  font-size: calc(18px + (28 - 18) * ((100vw - 300px) / (1800 - 300)));
  line-height: calc(22px + (32 - 22) * ((100vw - 300px) / (1800 - 300)));
  color: #fff;
  background-color: #265573;
  border-radius: 10px;
  border: 3px solid #9AD2F4;

  &:hover {
    color: #265573;
    background-color: #9AD2F4;
      border: 3px solid #fff;
      transition: 0.3s;
  }
}